import { useParams } from 'react-router-dom';
import {useEffect,useState} from 'react';
import {Navbar, Nav, NavDropdown, Form, FormControl, Container} from 'react-bootstrap';
import {
  BrowserRouter as Router,Routes,Switch,Route,Link,useNavigate,Outlet
} from "react-router-dom";
import { Alert } from 'bootstrap';


function Detail(props) {
  let [count, setCount] = useState(0);
  //let [style, setStyle] = useState({diplay:'block'});
  let [alert, setAlert] = useState(true);
  let [countDown, setCountDown] = useState(5); // 초기값 3 세팅.  countDown is count down
  
  useEffect(() => { 
    // useEffect hook to get the data from the API
    // mount , update exectued this function
    console.log("@@ useEffect는 html 랜더링 ##후## 실행됨");
    let a = setTimeout(() => {setAlert(false);console.log('Alert=',alert);}, 5000);
    
    console.log(2);

    return ()=>{

      console.log("@@ CleanUp @@ useEffect는 html 랜더링 ##전에## 실행됨");
      console.log(1);
      // clean up
      clearTimeout(a);
      //clearTimeout(b);
      // 서버에 시간이 소요되는 데이터를 요청시 사용
      // 서버에서 데이터를 받아오는 작업을 수행하는 과정
      // 과정중 재 랜더링으로 인하 중복 실행을 방지
    }
  }, [alert]); // just once ececuted
  //}, [count]); // condition: changed , dependency array
  
  // before import React-Router-Dom 
  let navigate = useNavigate(); // useNavigate() is a hook for 
  let {id} = useParams();
  console.log('@@ useParams id=',id);

    return (
      setTimeout(() => { // countDown -1 every second
        if(countDown > 0){
        setCountDown(countDown-1);
        console.log('countDown=',countDown);
        }
      }, 1000),
      console.log('@@ Detail.js props.shoes=',props.shoes),
      <>
       <div className="container">
       {/* <div className="alert alert-warning" style={style}>2초이내 구매시 할인!</div> */}
       { alert === true ?
       <div className="alert alert-warning">#깜짝세일#  {countDown}초 이내 구매시 50% 할인^^ 여기를 클릭 #</div>
       :  null
       }
       {/* {count} <button onClick={()=>{
          setCount(count+1);
       }}>count Up</button> */}
    <div className="row">
      <div className="col-md-6">
        <img src={'https://codingapple1.github.io/shop/shoes'+(props.shoes[id].id+1)+'.jpg'} alt='' width='100%' />
      </div>
      <div className="col-md-6">
        <h4 className="pt-5">{props.shoes[id].title}</h4>
        <p>{props.shoes[id].content}</p>
        <p>{props.shoes[id].price}</p>
        <button className="btn btn-danger">주문하기</button> 
          <div>
          <Nav.Link onClick={()=>{navigate(-1)}} className='px-3'>목록 보기</Nav.Link> 
        </div>
      </div>

    </div>
  </div> 
       </>
    ) 
  }  // end of Detail

  export default Detail;